:root {
  --color-primary-light: #e74c3c;
  --color-primary-light-2: #c0392b;
  --color-primary-dark: #041230;
  --color-t-light: #f5f6fa;
  --color-t-light-2: #dcdde1;
  --color-t-light-3: #fff;
  --color-t-dark: #1e272e;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  background: var(--color-typography-light);
  scroll-behavior: smooth;
}

.container {
  max-width: 100rem;
  margin: 0 auto;
}

body {
  color: var(--color-t-dark);
  font-family: "Montserrat", sans-serif;
}

@keyframes startButton {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes leftToRight {
  0% {
    opacity: 0;
    transform: translateX(-50rem);
  }
  80% {
    transform: translateX(5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.u-margin-top-small {
  margin-top: 1rem;
}

.u-margin-top-medium {
  margin-top: 2.5rem;
}

.u-margin-top-big {
  margin-top: 4rem;
}

.footer {
  padding: 5rem 0;
  background: var(--color-primary-dark);
}
.footer p {
  text-align: center;
  color: var(--color-t-light-3);
}

.navigation {
  flex: 0 15%;
  background: var(--color-primary-dark);
  color: var(--color-t-light-3);
  width: 15%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
}
@media only screen and (max-width: 26em) {
  .navigation {
    width: 100%;
    height: 8rem;
    z-index: 10000;
  }
}
.navigation .logo {
  font-size: 8rem;
  margin-bottom: 10rem;
  border-bottom: 1px solid var(--color-t-light-3);
  width: 100%;
  text-align: center;
  margin-top: 5rem;
}
@media only screen and (max-width: 26em) {
  .navigation .logo {
    font-size: 2.5rem;
    border: none;
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    padding: 0 4rem;
    align-items: center;
    margin-top: 3rem;
  }
}
.navigation .logo .mobile-menu {
  display: none;
  cursor: pointer;
}
@media only screen and (max-width: 26em) {
  .navigation .logo .mobile-menu {
    display: flex;
  }
}
.navigation .logo i {
  margin-bottom: 5rem;
}
@media only screen and (max-width: 26em) {
  .navigation .logo i {
    margin: 0;
  }
}
.navigation .desc {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 4rem;
}
@media only screen and (max-width: 26em) {
  .navigation .desc {
    background: var(--color-primary-dark);
    height: auto;
  }
}
@media only screen and (max-width: 26em) {
  .navigation .desc .list {
    background: var(--color-primary-dark);
  }
}
.navigation .desc .list-item {
  list-style: none;
  margin-bottom: 3rem;
}
.navigation .desc .list-item-link {
  text-decoration: none;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: var(--color-t-light-3);
  font-weight: 500;
  display: block;
}
@media only screen and (max-width: 26em) {
  .navigation .desc .list-item-link {
    width: 100%;
  }
}
.navigation .desc .list-item-link:hover {
  color: var(--color-primary-light-2);
  transition: all 0.2s ease-out;
}
.navigation .desc .social-links a {
  font-size: 1.7rem;
  color: var(--color-t-light-3);
  text-decoration: none;
  margin-right: 1rem;
}
.navigation .desc .social-links a:hover {
  color: var(--color-primary-light-2);
  transition: all 0.2s ease-out;
}

.container-portfolio {
  position: relative;
}

.pages {
  flex: 0 85%;
  width: 85%;
  margin-left: 15%;
  overflow: auto;
}
@media only screen and (max-width: 26em) {
  .pages {
    width: 100%;
    margin-top: 8rem;
    margin-left: 0;
  }
}
.pages .main-page {
  height: 100vh;
  position: relative;
}
.pages .main-page .background-image img {
  object-fit: cover;
  width: 100%;
  height: 100vh;
}
.pages .main-page .absolute-info {
  position: absolute;
  z-index: 100;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.pages .main-page .absolute-info .image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  background-color: blue;
  margin: auto;
  border: 5px solid #fff;
}
.pages .main-page .absolute-info .image img {
  width: 100%;
  transform: scale(1.4);
}
.pages .main-page .absolute-info .desc {
  text-align: center;
  margin-top: 3rem;
  color: var(--color-t-light-3);
}
.pages .main-page .absolute-info .desc h1 {
  font-size: 5rem;
  text-transform: uppercase;
  font-weight: 400;
}
.pages .main-page .absolute-info .desc h1 span {
  color: var(--color-primary-light-2);
}
.pages .main-page .absolute-info .desc p {
  margin-top: 0.5rem;
  font-size: 2.5rem;
}
.pages .main-page .absolute-info .desc p span {
  font-weight: 600;
}
.pages .about-me-page {
  margin-top: 10rem;
}
@media only screen and (max-width: 26em) {
  .pages .about-me-page {
    padding: 1rem;
  }
}
.pages .about-me-page h1 {
  font-size: 4rem;
  text-transform: uppercase;
  border-left: 0.8rem solid var(--color-primary-light-2);
  padding-left: 1rem;
  margin-bottom: 7rem;
}
.pages .about-me-page-desc {
  display: flex;
}
.pages .about-me-page-desc .left {
  flex: 0 40%;
}
@media only screen and (max-width: 26em) {
  .pages .about-me-page-desc .left {
    display: none;
  }
}
.pages .about-me-page-desc .left .image img {
  width: 100%;
}
.pages .about-me-page-desc .right {
  flex: 0 60%;
  margin-left: 2rem;
}
@media only screen and (max-width: 26em) {
  .pages .about-me-page-desc .right {
    margin-left: 0;
    flex: 0 100%;
  }
}

.pages .about-me-page-desc .right h2 {
  font-size: 2.5rem;
  font-weight: 500;
  margin-bottom: 3rem;
}
@media only screen and (max-width: 26em) {
  .pages .about-me-page-desc .right h2 {
    font-size: 3.1rem;
  }
}
.pages .about-me-page-desc .right h2 span {
  color: var(--color-primary-light-2);
}
.pages .about-me-page-desc .right p {
  font-size: 1.6rem;
  line-height: 1.6;
}
.pages .about-me-page-desc .right .right-infos {
  margin-top: 2rem;
  display: flex;
  margin-bottom: 2rem;
}
.pages .about-me-page-desc .right .right-infos p {
  margin-bottom: 1rem;
}
.pages .about-me-page-desc .right .right-infos span {
  font-weight: 600;
  margin-right: 1rem;
}
.pages .about-me-page-desc .right .right-infos-left {
  flex: 0 50%;
}
.pages .about-me-page-desc .right .right-infos-right {
  flex: 0 50%;
}
.pages .my-projects {
  margin-top: 10rem;
  margin-bottom: 10rem;
}
@media only screen and (max-width: 26em) {
  .pages .my-projects {
    padding: 1rem;
    margin-top: 5rem;
  }
}
.pages .my-projects h1 {
  font-size: 4rem;
  text-transform: uppercase;
  border-left: 0.8rem solid var(--color-primary-light-2);
  padding-left: 1rem;
  margin-bottom: 7rem;
}
.pages .my-projects .projects {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
}
@media only screen and (max-width: 26em) {
  .pages .my-projects .projects {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 1rem;
  }
}
.pages .my-projects .projects .project {
  position: relative;
}
.pages .my-projects .projects .project img {
  width: 100%;
  height: 35rem;
  object-fit: cover;
  box-shadow: 0px 0px 5px -1px rgba(0, 0, 0, 0.75);
}
.pages .my-projects .projects .project .name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  color: var(--color-t-light-3);
  z-index: 100;
  display: none;
}
.pages .my-projects .projects .project:hover {
  outline: 1px solid white;
  outline-offset: -5rem;
  transition: all 0.2s ease-out;
}
.pages .my-projects .projects .project:hover .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #000;
  opacity: 0.9;
  transition: all 0.2s ease-out;
}
.pages .my-projects .projects .project:hover .name {
  display: block;
  transition: all 0.5s ease-out;
}
@media only screen and (max-width: 26em) {
  .pages .project-info {
    padding: 1rem;
  }
}
.pages .project-info h1 {
  margin-top: 5rem;
  font-size: 5rem;
  text-transform: uppercase;
  font-weight: 500;
}
.pages .project-info .buttons {
  margin-top: 2rem;
  display: flex;
  font-size: 1.3rem;
}
.pages .project-info .buttons .button-preview {
  text-decoration: none;
  padding: 1rem 2rem;
  background: var(--color-primary-light-2);
  color: var(--color-t-light-3);
  width: 15rem;
  text-align: center;
  margin-right: 2rem;
  border-radius: 3px;
  border: 3px solid var(--color-primary-light-2);
}
.pages .project-info .buttons .button-preview:hover {
  background: transparent;
  color: var(--color-primary-light-2);
  transition: all 0.2s ease-out;
}
.pages .project-info .buttons .button-github {
  text-decoration: none;
  padding: 1rem 2rem;
  background: #333;
  color: var(--color-t-light-3);
  width: 15rem;
  text-align: center;
  border-radius: 3px;
  border: 3px solid #333;
}
.pages .project-info .buttons .button-github:hover {
  background: transparent;
  color: #333;
  transition: all 0.2s ease-out;
}
.pages .project-info .desc {
  margin-top: 5rem;
  font-size: 1.6rem;
  line-height: 1.6;
  margin-bottom: 5rem;
}
.pages .project-info .images {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  margin-top: 10rem;
  margin-bottom: 10rem;
}
.pages .project-info .images .image img {
  height: 20rem;
  width: 100%;
  object-fit: scale-down;
}
.pages .project-info .images-full .image {
  margin-bottom: 2rem;
}
.pages .project-info .images-full .image:last-child {
  margin-bottom: 5rem;
}
.pages .project-info .images-full .image img {
  width: 100%;
  box-shadow: 0px 0px 7px -3px rgba(0, 0, 0, 0.75);
}
.button-cw {
  text-decoration: none;
  padding: 1rem 2rem;
  color: var(--color-t-light-3);
  font-size: 1.4rem;
  border: 2px solid transparent;
  background: var(--color-primary-light-2);
  margin-bottom: 3rem;
  display: inline-block;
}
.button-cw:hover {
  transition: all 0.2s ease-out;
  background: transparent;
  border: 2px solid var(--color-primary-light-2);
  color: var(--color-primary-light-2);
}
